// ----------------------------------------------------
// INTERFACES (Gi nguyn)
// ----------------------------------------------------

export interface User {
  id: string;
  email: string;
  name: string;
  photoURL?: string;
  totalPoints: number;
  streak: number;
  totalWords: number;
  lastUpdated: Timestamp;
}

export interface VocabularySet {
  id: string;
  name: string;
  description: string;
  createdAt: Timestamp;
  userId: string;
  totalWords: number; // Dng  hin th s lng t
}

export interface VocabularyWord {
  id: string;
  kanji: string;
  kana: string;
  meaning: string;
  notes?: string;
  difficulty: number; // 0-100 (score)
  lastReviewed?: Timestamp;
}

export interface LearningSessionHistory {
  id: string;
  date: Timestamp;
  correctAnswers: number;
  totalAttempts: number;
  wordUpdates: Array<{ wordId: string; newDifficulty: number; oldDifficulty: number }>;
}

// ----------------------------------------------------
// FIRESTORE USER DATA (Gi nguyn)
// ----------------------------------------------------

/**
 * Lu/Cp nht d liu ngi dng mi/hin c
 */
export const saveUserData = async (
  userId: string, 
  email: string, 
  name: string, 
  photoURL?: string
) => {
  try {
    const userDocRef = doc(db, 'users', userId);
    // S dng setDoc vi merge: true  ch to nu cha tn ti
    await setDoc(userDocRef, {
      email,
      name,
      photoURL: photoURL || null,
      totalPoints: 0,
      streak: 0,
      totalWords: 0,
      createdAt: Timestamp.now(),
    }, { merge: true });
  } catch (error) {
    console.error('Save user data error:', error);
    throw new Error('Lu d liu ngi dng tht bi');
  }
};

/**
 * Ti d liu ngi dng t Firestore
 */
export const getUserData = async (userId: string): Promise<User | null> => {
  try {
    const userDocRef = doc(db, 'users', userId);
    const userSnap = await getDoc(userDocRef);

    if (userSnap.exists()) {
      return {
        id: userSnap.id,
        ...userSnap.data(),
      } as User;
    }
    return null;
  } catch (error) {
    console.error('Get user data error:', error);
    throw new Error('Ti d liu ngi dng tht bi');
  }
};

// ----------------------------------------------------
// FIRESTORE VOCABULARY SETS
// ----------------------------------------------------

/**
 * Ti danh sch b t vng ca ngi dng (ng dn Subcollection  ng)
 */
export const getVocabularySets = async (userId: string): Promise<VocabularySet[]> => {
  try {
    const setsRef = collection(db, 'users', userId, 'vocabularySets');
    const setsSnap = await getDocs(setsRef);

    return setsSnap.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
    })) as VocabularySet[];
  } catch (error) {
    console.error('Error getting vocabulary sets:', error);
    throw new Error('Ti b t tht bi');
  }
};

/**
 *   SA CHA: Thm b t vng mi v tr v i tng b t  c ID.
 *  FIX: m bo trng totalWords: 0 (hoc wordCount: 0) c thm vo payload ghi DB.
 * @returns i tng VocabularySet va c to (cng vi ID)
 */
export const addVocabularySet = async (
  userId: string, 
  name: string, 
  description: string
): Promise<VocabularySet> => {
  try {
    // Chun b d liu b t mi
    const newSet = { //  SA 1: KHNG DNG Omit<..., 'totalWords'> NA
      name,
      description,
      userId,
      createdAt: Timestamp.now(),
      totalWords: 0, //  SA 2: THM TRNG TOTALWORDS VO PAYLOAD GHI DB
    };

    // ng dn subcollection: users/{userId}/vocabularySets
    const setDocRef = await addDoc(collection(db, 'users', userId, 'vocabularySets'), newSet);

    // Tr v i tng VocabularySet hon chnh (c ID)
    return {
      id: setDocRef.id,
      ...newSet
    } as VocabularySet;

  } catch (error) {
    console.error('Error adding vocabulary set:', error);
    throw new Error('Thm b t tht bi');
  }
};

/**
 * Cp nht tn v m t ca b t vng
 */
export const updateVocabularySet = async (
  userId: string, 
  setId: string, 
  name: string, 
  description: string
) => {
  try {
    const setDocRef = doc(db, 'users', userId, 'vocabularySets', setId);
    await updateDoc(setDocRef, { name, description });
  } catch (error) {
    console.error('Error updating vocabulary set:', error);
    throw new Error('Cp nht b t tht bi');
  }
};

/**
 * Xa mt b t vng. Cn xa TT C cc t con trong subcollection 'words'
 */
export const deleteVocabularySet = async (userId: string, setId: string) => {
  try {
    const batch = writeBatch(db);
    const setDocRef = doc(db, 'users', userId, 'vocabularySets', setId);
    
    // 1. Xa TT C cc t con trong subcollection 'words' (rt quan trng!)
    const wordsRef = collection(setDocRef, 'words');
    const wordsSnap = await getDocs(wordsRef);
    wordsSnap.docs.forEach(wordDoc => {
      batch.delete(wordDoc.ref);
    });

    // 2. Xa ti liu VocabularySet chnh
    batch.delete(setDocRef);

    // 3. Commit batch
    await batch.commit();

  } catch (error) {
    console.error('Error deleting vocabulary set:', error);
    throw new Error('Xa b t tht bi');
  }
};


// ----------------------------------------------------
// FIRESTORE VOCABULARY WORDS
// ----------------------------------------------------

/**
 * Ti cc t vng thuc mt b t (Subcollection: words)
 */
export const getVocabularyWords = async (userId: string, setId: string): Promise<VocabularyWord[]> => {
  try {
    // ng dn Subcollection: users/{userId}/vocabularySets/{setId}/words
    const wordsRef = collection(db, 'users', userId, 'vocabularySets', setId, 'words');
    // Sp xp theo difficulty (v d)
    const q = query(wordsRef, orderBy('difficulty', 'asc')); 
    const wordsSnap = await getDocs(q);

    return wordsSnap.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
    })) as VocabularyWord[];

  } catch (error) {
    console.error('Error getting vocabulary words:', error);
    throw new Error('Ti t vng tht bi');
  }
};

/**
 * Thm t vng mi vo b t (Subcollection: words)
 */
export const addVocabularyWord = async (
  userId: string, 
  setId: string, 
  word: Omit<VocabularyWord, 'id' | 'lastReviewed'>
): Promise<VocabularyWord> => {
  try {
    const wordsRef = collection(db, 'users', userId, 'vocabularySets', setId, 'words');
    const newWordPayload = {
      ...word,
      difficulty: 50, // Khi to  kh trung bnh (0-100)
      lastReviewed: null,
      createdAt: Timestamp.now(),
    };
    
    const wordDocRef = await addDoc(wordsRef, newWordPayload);

    // Tng trng totalWords trong ti liu VocabularySet chnh
    const setDocRef = doc(db, 'users', userId, 'vocabularySets', setId);
    await updateDoc(setDocRef, {
        totalWords: increment(1)
    });

    return {
      id: wordDocRef.id,
      ...newWordPayload
    } as VocabularyWord;

  } catch (error) {
    console.error('Error adding vocabulary word:', error);
    throw new Error('Thm t vng tht bi');
  }
};

/**
 * Cp nht mt t vng
 */
export const updateVocabularyWord = async (
  userId: string, 
  setId: string, 
  wordId: string, 
  updates: Partial<VocabularyWord>
) => {
  try {
    const wordDocRef = doc(db, 'users', userId, 'vocabularySets', setId, 'words', wordId);
    await updateDoc(wordDocRef, updates);
  } catch (error) {
    console.error('Error updating vocabulary word:', error);
    throw new Error('Cp nht t vng tht bi');
  }
};

/**
 * Xa mt t vng
 */
export const deleteVocabularyWord = async (userId: string, setId: string, wordId: string) => {
  try {
    // 1. Xa ti liu t vng
    const wordDocRef = doc(db, 'users', userId, 'vocabularySets', setId, 'words', wordId);
    await deleteDoc(wordDocRef);

    // 2. Gim trng totalWords trong ti liu VocabularySet chnh
    const setDocRef = doc(db, 'users', userId, 'vocabularySets', setId);
    await updateDoc(setDocRef, {
        totalWords: increment(-1)
    });
  } catch (error) {
    console.error('Error deleting vocabulary word:', error);
    throw new Error('Xa t vng tht bi');
  }
};

// ----------------------------------------------------
// FIRESTORE LEARNING SESSIONS
// ----------------------------------------------------

/**
 * Lu kt qu mt phin hc
 */
export const saveLearningSession = async (userId: string, sessionData: Omit<LearningSessionHistory, 'id'>) => {
  try {
    const sessionsRef = collection(db, 'users', userId, 'learningSessions');
    await addDoc(sessionsRef, {
      ...sessionData,
      date: Timestamp.now()
    });
  } catch (error) {
    console.error('Save learning session error:', error);
    throw new Error('Lu phin hc tht bi');
  }
};

/**
 * Ti lch s cc phin hc (dng cho component Statistics.tsx).
 */
export const getLearningSessions = async (userId: string): Promise<LearningSessionHistory[]> => {
  try {
    const sessionsRef = collection(db, 'users', userId, 'learningSessions');
    const q = query(sessionsRef, orderBy('date', 'desc'));
    const sessionsSnap = await getDocs(q);

    return sessionsSnap.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    })) as LearningSessionHistory[];
  } catch (error) {
    console.error('Error getting learning sessions:', error);
    throw new Error('Ti phin hc tht bi');
  }
}

