import {  Route, Routes, Outlet } from 'react-router-dom'
// NOTE: Ti  thay th 'react-router' thnh 'react-router-dom' v y l package tiu chun cho mi trng web.

// 1. IMPORT CC TRANG CA BN (c gi nguyn)
import HomePage from './pages/Home' 
import SettingsPage from './pages/Settings'        
import StatisticsPage from './pages/Statistics'    
import LearningMode from './components/LearningMode'    
import VocabularyManager from './components/VocabularyManager' 

// 2. IMPORT COMPONENT B CC CHUNG (Shell)
// Component ny phi c TO MI (xem ghi ch quan trng bn di)
import Shell from './components/Shell' 


export default function App() {
  return (
    // XA th <BrowserRouter>  y
    <Routes>
      <Route path="/" element={<Shell />}>
        <Route index element={<HomePage />} />
        <Route path="settings" element={<SettingsPage />} />
        <Route path="statistics" element={<StatisticsPage />} />
        <Route path="learn" element={<LearningMode />} />
        <Route path="vocabulary" element={<VocabularyManager />} />
      </Route>
    </Routes>
    // XA th </BrowserRouter>  y
  )
}