/**
 * Vocabulary Manager Component
 * Handles adding, editing, and deleting vocabulary words and sets
 * Integrated with Firebase Firestore
 */

import { useState, useEffect, useCallback } from 'react'
import { Button } from './ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card'
import { Input } from './ui/input'
import { Label } from './ui/label'
import { Textarea } from './ui/textarea'
import { Badge } from './ui/badge'
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle, 
  DialogTrigger, 
  DialogDescription //  SA:  thm DialogDescription  khc phc ReferenceError
} from './ui/dialog'
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs'
import { useToast } from '../hooks/use-toast' 
import { 
  Plus, 
  Edit2, 
  Trash2, 
  BookOpen,
  Hash,
  Loader2,
  List,
  MessageSquare,
  Import
} from 'lucide-react'

import { useAuth } from './AuthProvider' 

//  IMPORT HM FIREBASE THC T
import { 
  getVocabularySets, 
  addVocabularySet, 
  updateVocabularySet, 
  deleteVocabularySet, 
  getVocabularyWords,
  addVocabularyWord, 
  updateVocabularyWord,
  deleteVocabularyWord
} from '../lib/firebase' 
import type { VocabularySet, VocabularyWord } from '../lib/firebase' // Import interfaces

// ----------------------------------------------------
// INTERFACES (c gi nguyn)

export default function VocabularyManager() {
  const { user } = useAuth()
  const { toast } = useToast()

  const [vocabularySets, setVocabularySets] = useState<VocabularySet[]>([])
  const [selectedSet, setSelectedSet] = useState<VocabularySet | null>(null)
  const [vocabularyWords, setVocabularyWords] = useState<VocabularyWord[]>([])

  const [isLoading, setIsLoading] = useState(false)
  const [isCreateSetOpen, setIsCreateSetOpen] = useState(false)
  
  // State cho form to b t
  const [newSetName, setNewSetName] = useState('')
  const [newSetDescription, setNewSetDescription] = useState('')

  // State cho form thm t
  const [newWordKanji, setNewWordKanji] = useState('')
  const [newWordKana, setNewWordKana] = useState('')
  const [newWordMeaning, setNewWordMeaning] = useState('')
  const [newWordNotes, setNewWordNotes] = useState('')
  
  // State cho form chnh sa t (dng chung)
  const [isEditWordOpen, setIsEditWordOpen] = useState(false)
  const [editingWord, setEditingWord] = useState<VocabularyWord | null>(null)
  const [editKanji, setEditKanji] = useState('')
  const [editKana, setEditKana] = useState('')
  const [editMeaning, setEditMeaning] = useState('')
  const [editNotes, setEditNotes] = useState('')

  // State cho Bulk Import
  const [isBulkImportOpen, setIsBulkImportOpen] = useState(false)
  const [bulkData, setBulkData] = useState('')
  const [bulkImportProgress, setBulkImportProgress] = useState<string | null>(null)


  // ----------------------------------------------------
  // I. LOGIC TI D LIU
  // ----------------------------------------------------

  // 1. Ti danh sch b t
  const fetchSets = useCallback(async () => {
    if (!user) return
    setIsLoading(true)
    try {
      const sets = await getVocabularySets(user.id)
      setVocabularySets(sets)
      // Nu khng c b t no c chn trc, chn b t u tin (nu c)
      if (!selectedSet && sets.length > 0) {
        setSelectedSet(sets[0])
      }
    } catch (error) {
      console.error('Error fetching vocabulary sets:', error)
      toast({
        title: 'Li',
        description: 'Khng th ti danh sch b t.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, toast])

  // 2. Ti cc t trong b t  chn
  const fetchWords = useCallback(async (setId: string) => {
    setIsLoading(true)
    try {
      const words = await getVocabularyWords(user!.id, setId)
      setVocabularyWords(words)
    } catch (error) {
      console.error('Error fetching vocabulary words:', error)
      toast({
        title: 'Li',
        description: 'Khng th ti t vng.',
        variant: 'destructive',
      })
      setVocabularyWords([]) // Xa danh sch nu li
    } finally {
      setIsLoading(false)
    }
  }, [user, toast])

  // useEffect  ti danh sch b t ban u
  useEffect(() => {
    fetchSets()
  }, [fetchSets]) 

  // useEffect  ti t vng khi b t c chn thay i
  useEffect(() => {
    if (selectedSet) {
      fetchWords(selectedSet.id)
    } else {
      setVocabularyWords([])
    }
  }, [selectedSet, fetchWords])


  // ----------------------------------------------------
  // II. LOGIC QUN L B T (VOCABULARY SETS)
  // ----------------------------------------------------

  //  SA: Cp nht hm ny  gii quyt li to b t khng hin th
  const handleCreateSet = useCallback(async (e: React.FormEvent) => {
    e.preventDefault()
    if (!newSetName.trim() || !user) return

    setIsLoading(true)
    try {
      // 1. GI HM TO T FIREBASE V LY V I TNG B T MI
      const newSet = await addVocabularySet(
        user.id,
        newSetName.trim(),
        newSetDescription.trim()
      )

      //  BC SA 1: CP NHT STATE VI B T MI
      setVocabularySets((prevSets) => [...prevSets, newSet])
      
      //  BC SA 2: CHN B T MI NGAY LP TC
      setSelectedSet(newSet) 
      
      // Reset form v ng dialog
      setNewSetName('')
      setNewSetDescription('')
      setIsCreateSetOpen(false) 
      
      toast({
        title: 'Thnh cng!',
        description: ` to b t "${newSet.name}". B t  c chn.`,
      })
      
    } catch (error) {
      console.error('Error creating vocabulary set:', error)
      toast({
        title: 'Li',
        description: 'Khng th to b t. Vui lng th li.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, newSetName, newSetDescription, toast])


  const handleDeleteSet = useCallback(async (setId: string, setName: string) => {
    if (!user || !confirm(`Bn c chc chn mun xa b t "${setName}" v tt c t vng ca n?`)) return

    setIsLoading(true)
    try {
      await deleteVocabularySet(user.id, setId)
      
      // Cp nht state cc b
      setVocabularySets(prevSets => prevSets.filter(set => set.id !== setId))
      
      // Nu b t ang chn b xa, hy chn
      if (selectedSet && selectedSet.id === setId) {
        setSelectedSet(null)
        setVocabularyWords([])
      }
      
      toast({
        title: 'Thnh cng!',
        description: ` xa b t "${setName}".`,
      })

    } catch (error) {
      console.error('Error deleting vocabulary set:', error)
      toast({
        title: 'Li',
        description: 'Khng th xa b t. Vui lng th li.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, toast])

  // Logic cp nht b t (gi nguyn)
  const handleUpdateSet = useCallback(async (setId: string, newName: string, newDescription: string) => {
    if (!user) return
    
    setIsLoading(true)
    try {
      await updateVocabularySet(user.id, setId, { name: newName, description: newDescription })

      // Cp nht state cc b
      setVocabularySets(prevSets => prevSets.map(set => 
        set.id === setId ? { ...set, name: newName, description: newDescription } : set
      ))
      
      // Cp nht selectedSet
      if (selectedSet && selectedSet.id === setId) {
        setSelectedSet(prevSet => prevSet ? { ...prevSet, name: newName, description: newDescription } : null)
      }

      toast({
        title: 'Thnh cng!',
        description: ' cp nht b t.',
      })

    } catch (error) {
      console.error('Error updating vocabulary set:', error)
      toast({
        title: 'Li',
        description: 'Khng th cp nht b t.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, toast])

  // ----------------------------------------------------
  // III. LOGIC QUN L T VNG (VOCABULARY WORDS)
  // ----------------------------------------------------

  const handleAddWord = useCallback(async (e: React.FormEvent) => {
    e.preventDefault()
    if (!selectedSet || !user || !newWordKanji.trim() || !newWordMeaning.trim()) return

    setIsLoading(true)
    try {
      const newWord: Omit<VocabularyWord, 'id' | 'difficulty'> = {
        kanji: newWordKanji.trim(),
        kana: newWordKana.trim(),
        meaning: newWordMeaning.trim(),
        notes: newWordNotes.trim(),
      }
      
      // Thm t vo Firestore v nhn li i tng t  c ID
      const wordWithId = await addVocabularyWord(user.id, selectedSet.id, newWord)
      
      // Cp nht state cc b
      setVocabularyWords(prevWords => [wordWithId, ...prevWords])
      
      // Cp nht s lng t trong b t ang chn (Nu totalWords cha c update t ng)
      if (selectedSet) {
        setSelectedSet(prevSet => prevSet ? { ...prevSet, totalWords: (prevSet.totalWords || 0) + 1 } : null)
        setVocabularySets(prevSets => prevSets.map(set => 
          set.id === selectedSet.id ? { ...set, totalWords: (set.totalWords || 0) + 1 } : set
        ))
      }

      // Reset form
      setNewWordKanji('')
      setNewWordKana('')
      setNewWordMeaning('')
      setNewWordNotes('')

      toast({
        title: 'Thnh cng!',
        description: ` thm t: ${wordWithId.kanji}`,
      })
      
    } catch (error) {
      console.error('Error adding vocabulary word:', error)
      toast({
        title: 'Li',
        description: 'Khng th thm t vng.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, newWordKanji, newWordKana, newWordMeaning, newWordNotes, toast])

  // Logic chnh sa t (gi nguyn)
  const handleUpdateWord = useCallback(async (e: React.FormEvent) => {
    e.preventDefault()
    if (!selectedSet || !user || !editingWord) return

    setIsLoading(true)
    try {
      const updatedData = {
        kanji: editKanji.trim(),
        kana: editKana.trim(),
        meaning: editMeaning.trim(),
        notes: editNotes.trim(),
      }
      
      await updateVocabularyWord(user.id, selectedSet.id, editingWord.id, updatedData)

      // Cp nht state cc b
      setVocabularyWords(prevWords => prevWords.map(word => 
        word.id === editingWord.id ? { ...word, ...updatedData } as VocabularyWord : word
      ))
      
      // ng dialog
      setIsEditWordOpen(false)
      setEditingWord(null)

      toast({
        title: 'Thnh cng!',
        description: ` cp nht t: ${editKanji}`,
      })

    } catch (error) {
      console.error('Error updating vocabulary word:', error)
      toast({
        title: 'Li',
        description: 'Khng th cp nht t vng.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, editingWord, editKanji, editKana, editMeaning, editNotes, toast])

  const handleDeleteWord = useCallback(async (setId: string, wordId: string) => {
    if (!user || !confirm('Bn c chc chn mun xa t vng ny?')) return

    setIsLoading(true)
    try {
      await deleteVocabularyWord(user.id, setId, wordId)
      
      // Cp nht state cc b
      setVocabularyWords(prevWords => prevWords.filter(word => word.id !== wordId))
      
      // Cp nht s lng t trong b t ang chn (Nu totalWords cha c update t ng)
      if (selectedSet && selectedSet.id === setId) {
        setSelectedSet(prevSet => prevSet ? { ...prevSet, totalWords: Math.max(0, (prevSet.totalWords || 0) - 1) } : null)
        setVocabularySets(prevSets => prevSets.map(set => 
            set.id === setId ? { ...set, totalWords: Math.max(0, (set.totalWords || 0) - 1) } : set
        ))
      }

      toast({
        title: 'Thnh cng!',
        description: ' xa t vng.',
      })

    } catch (error) {
      console.error('Error deleting vocabulary word:', error)
      toast({
        title: 'Li',
        description: 'Khng th xa t vng. Vui lng th li.',
        variant: 'destructive',
      })
    } finally {
      setIsLoading(false)
    }
  }, [user, selectedSet, toast])

  // ----------------------------------------------------
  // IV. LOGIC BULK IMPORT (Gi nguyn)
  // ----------------------------------------------------

  const handleBulkImport = useCallback(async (e: React.FormEvent) => {
    e.preventDefault()
    if (!selectedSet || !user || !bulkData.trim()) return

    const lines = bulkData.trim().split('\n').filter(line => line.trim() !== '')
    const newWords: Omit<VocabularyWord, 'id' | 'difficulty'>[] = []
    
    // Format: Kanji,Kana,Meaning,Notes (Notes l ty chn, ch ly 3 trng chnh)
    for (const line of lines) {
        const parts = line.split(',').map(p => p.trim());
        if (parts.length >= 3) {
            newWords.push({
                kanji: parts[0],
                kana: parts[1] || '',
                meaning: parts[2],
                notes: parts[3] || '',
            });
        }
    }

    if (newWords.length === 0) {
        toast({
            title: 'Li nh dng',
            description: 'Khng tm thy t vng hp l. Vui lng s dng nh dng: Kanji,Kana,Meaning',
            variant: 'destructive',
        });
        return;
    }

    setBulkImportProgress(`ang nhp ${newWords.length} t...`)

    try {
        // S dng batch write  thm nhiu t mt lc (gi nh hm  c trong firebase.ts)
        // LU : addVocabularyWord.bulk l gi nh, bn cn m bo hm ny c trong '../lib/firebase'
        const addedWords = await (addVocabularyWord as any).bulk(user.id, selectedSet.id, newWords)
        
        // Cp nht state cc b
        setVocabularyWords(prevWords => [...addedWords, ...prevWords])
        
        // Cp nht s lng t trong b t ang chn
        if (selectedSet) {
            setSelectedSet(prevSet => prevSet ? { ...prevSet, totalWords: (prevSet.totalWords || 0) + addedWords.length } : null)
            setVocabularySets(prevSets => prevSets.map(set => 
                set.id === selectedSet.id ? { ...set, totalWords: (set.totalWords || 0) + addedWords.length } : set
            ))
        }

        toast({
            title: 'Thnh cng!',
            description: ` nhp thnh cng ${addedWords.length} t vo b t "${selectedSet.name}".`,
        })

        // Reset form v ng dialog
        setBulkData('')
        setIsBulkImportOpen(false)

    } catch (error) {
        console.error('Error during bulk import:', error)
        toast({
            title: 'Li',
            description: 'Khng th nhp t vng s lng ln. Vui lng th li.',
            variant: 'destructive',
        })
    } finally {
        setBulkImportProgress(null)
    }

  }, [user, selectedSet, bulkData, toast])

  // ----------------------------------------------------
  // V. RENDER
  // ----------------------------------------------------
  
  const totalWords = vocabularyWords.length

  // Hm helper  m dialog chnh sa
  const openEditDialog = (word: VocabularyWord) => {
    setEditingWord(word)
    setEditKanji(word.kanji)
    setEditKana(word.kana)
    setEditMeaning(word.meaning)
    setEditNotes(word.notes || '')
    setIsEditWordOpen(true)
  }


  if (!user) {
    return (
      <Card className="text-center shadow-lg w-full max-w-sm mx-auto">
        <CardHeader>
            <CardTitle>Yu cu ng nhp</CardTitle>
        </CardHeader>
        <CardContent>
            <p className="text-gray-500 mb-4">Bn cn ng nhp  qun l t vng.</p>
        </CardContent>
    </Card>
    )
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Qun l T vng</h1>
      <p className="text-gray-600 dark:text-gray-400">To, chnh sa v sp xp cc b t vng c nhn ca bn.</p>

      {/* DIALOG TO B T MI */}
      <Dialog open={isCreateSetOpen} onOpenChange={setIsCreateSetOpen}>
        <DialogTrigger asChild>
          <Button className="w-full md:w-auto" onClick={() => { 
            setNewSetName(''); 
            setNewSetDescription(''); 
            setIsCreateSetOpen(true); 
          }}>
            <Plus className="h-4 w-4 mr-2" /> To B T Mi
          </Button>
        </DialogTrigger>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>To B T Vng Mi</DialogTitle>
            <DialogDescription>
                t tn v m t cho b t mi ca bn.
            </DialogDescription>
          </DialogHeader>
          <form onSubmit={handleCreateSet} className="grid gap-4 py-4">
            <div className="grid gap-2">
              <Label htmlFor="setName">Tn b t <span className="text-red-500">*</span></Label>
              <Input
                id="setName"
                value={newSetName}
                onChange={(e) => setNewSetName(e.target.value)}
                placeholder="V d: N3 T vng, T chuyn ngnh IT"
                required
              />
            </div>
            <div className="grid gap-2">
              <Label htmlFor="setDescription">M t (Ty chn)</Label>
              <Textarea
                id="setDescription"
                value={newSetDescription}
                onChange={(e) => setNewSetDescription(e.target.value)}
                placeholder="M t ngn gn v b t ny"
              />
            </div>
            <Button type="submit" disabled={isLoading || !newSetName.trim()}>
              {isLoading ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : 'To B T'}
            </Button>
          </form>
        </DialogContent>
      </Dialog>


      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 h-[70vh]">
        {/* Ct 1: Danh sch B T */}
        <div className="md:col-span-1 flex flex-col space-y-4">
          <h2 className="text-xl font-semibold border-b pb-2 text-gray-800 dark:text-white">Danh sch B T ({vocabularySets.length})</h2>
          
          <div className="space-y-2 overflow-y-auto pr-2">
            {isLoading && vocabularySets.length === 0 ? (
              <div className="flex justify-center items-center h-24">
                <Loader2 className="h-6 w-6 animate-spin text-primary" />
              </div>
            ) : vocabularySets.length === 0 ? (
              <p className="text-gray-500 dark:text-gray-400 text-center p-4">
                Cha c b t no.
              </p>
            ) : (
              vocabularySets.map((set) => (
                <Card
                  key={set.id}
                  className={`cursor-pointer transition-all ${
                    selectedSet && selectedSet.id === set.id
                      ? 'border-primary shadow-lg bg-primary/10 dark:bg-primary/20'
                      : 'hover:border-primary/50 dark:hover:border-gray-600 bg-white dark:bg-gray-800'
                  }`}
                  onClick={() => setSelectedSet(set)}
                >
                  <CardContent className="p-4 flex justify-between items-start">
                    <div>
                      <CardTitle className="text-lg font-medium">
                        {set.name}
                      </CardTitle>
                      <CardDescription className="text-sm">
                        {set.description || 'Khng c m t'}
                      </CardDescription>
                      <Badge variant="secondary" className="mt-2">
                          <Hash className="h-3 w-3 mr-1" /> {set.totalWords || 0} t
                      </Badge>
                    </div>
                    <Button 
                      variant="ghost" 
                      size="icon"
                      className="text-red-500 hover:bg-red-50 dark:hover:bg-red-900/50"
                      onClick={(e) => {
                        e.stopPropagation()
                        handleDeleteSet(set.id, set.name)
                      }}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </CardContent>
                </Card>
              ))
            )}
          </div>
        </div>

        {/* Ct 2 & 3: Chi tit v Qun l T vng */}
        <div className="md:col-span-2">
            {selectedSet ? (
              <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg h-full">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <div className='flex items-center space-x-3'>
                    <BookOpen className="h-6 w-6 text-primary" />
                    <CardTitle className="text-2xl font-bold">{selectedSet.name}</CardTitle>
                  </div>
                  {/* Dialog chnh sa b t */}
                  <Dialog>
                    <DialogTrigger asChild>
                      <Button variant="outline" size="sm">
                        <Edit2 className="h-4 w-4 mr-2" /> Chnh sa
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="sm:max-w-[425px]">
                      <DialogHeader>
                        <DialogTitle>Chnh sa B T Vng</DialogTitle>
                        <DialogDescription>
                            Thay i tn v m t ca b t ny.
                        </DialogDescription>
                      </DialogHeader>
                      <EditSetForm 
                        set={selectedSet} 
                        onSave={handleUpdateSet}
                        isLoading={isLoading}
                      />
                    </DialogContent>
                  </Dialog>
                </CardHeader>
                <CardDescription className="px-6 pb-4">
                  {selectedSet.description || 'Khng c m t'}
                </CardDescription>

                {/* Tabs: Thm T / Nhp S Lng Ln / Danh sch T */}
                <CardContent className="p-6 pt-0">
                  <Tabs defaultValue="addWord" className="w-full">
                    <TabsList className="grid w-full grid-cols-3">
                      <TabsTrigger value="addWord">
                        <Plus className="h-4 w-4 mr-1" /> Thm T
                      </TabsTrigger>
                      <TabsTrigger value="wordList">
                        <List className="h-4 w-4 mr-1" /> Danh sch ({totalWords})
                      </TabsTrigger>
                      <TabsTrigger value="bulkImport" onClick={() => {setBulkData(''); setBulkImportProgress(null);}}>
                        <Import className="h-4 w-4 mr-1" /> Nhp SL
                      </TabsTrigger>
                    </TabsList>
                    
                    {/* Tab 1: Thm T */}
                    <TabsContent value="addWord" className="mt-4">
                      <form onSubmit={handleAddWord} className="grid grid-cols-2 gap-4">
                        <div className="space-y-2">
                          <Label htmlFor="kanji">Kanji/T <span className="text-red-500">*</span></Label>
                          <Input id="kanji" value={newWordKanji} onChange={(e) => setNewWordKanji(e.target.value)} required />
                        </div>
                        <div className="space-y-2">
                          <Label htmlFor="kana">Kana/Phin m</Label>
                          <Input id="kana" value={newWordKana} onChange={(e) => setNewWordKana(e.target.value)} />
                        </div>
                        <div className="space-y-2 col-span-2">
                          <Label htmlFor="meaning">Ngha <span className="text-red-500">*</span></Label>
                          <Input id="meaning" value={newWordMeaning} onChange={(e) => setNewWordMeaning(e.target.value)} required />
                        </div>
                        <div className="space-y-2 col-span-2">
                          <Label htmlFor="notes">Ghi ch (Ty chn)</Label>
                          <Textarea id="notes" value={newWordNotes} onChange={(e) => setNewWordNotes(e.target.value)} />
                        </div>
                        <Button type="submit" className="col-span-2" disabled={isLoading || !newWordKanji.trim() || !newWordMeaning.trim()}>
                          {isLoading ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : 'Thm T Vng'}
                        </Button>
                      </form>
                    </TabsContent>
                    
                    {/* Tab 2: Danh sch T */}
                    <TabsContent value="wordList" className="mt-4">
                      <div className="space-y-3 max-h-[30vh] overflow-y-auto pr-2">
                        {isLoading && totalWords === 0 ? (
                            <div className="flex justify-center items-center h-24">
                                <Loader2 className="h-6 w-6 animate-spin text-primary" />
                            </div>
                        ) : totalWords === 0 ? (
                          <p className="text-gray-500 dark:text-gray-400 text-center p-4">
                            B t ny cha c t vng no.
                          </p>
                        ) : (
                          vocabularyWords.map((word) => (
                            <Card key={word.id} className="bg-gray-50 dark:bg-gray-900 shadow-sm">
                              <CardContent className="p-3 flex justify-between items-center">
                                <div className="flex-1 min-w-0 pr-4">
                                  <p className="font-semibold text-lg truncate text-gray-900 dark:text-white">{word.kanji}</p>
                                  <p className="text-sm text-primary dark:text-primary-light truncate">{word.kana}</p>
                                  <p className="text-sm text-gray-600 dark:text-gray-300 truncate mt-1">{word.meaning}</p>
                                  {word.notes && (
                                    <div className="flex items-center text-xs text-gray-500 dark:text-gray-400 mt-1">
                                      <MessageSquare className="h-3 w-3 mr-1" />
                                      <span className="truncate">{word.notes}</span>
                                    </div>
                                  )}
                                </div>
                                <div className="flex space-x-1">
                                  <Button 
                                    variant="ghost" 
                                    size="icon"
                                    onClick={() => openEditDialog(word)}
                                    title="Chnh sa t"
                                  >
                                    <Edit2 className="h-4 w-4 text-primary" />
                                  </Button>
                                  <Button 
                                    variant="ghost" 
                                    size="icon"
                                    onClick={() => handleDeleteWord(selectedSet.id, word.id)}
                                    title="Xa t"
                                  >
                                    <Trash2 className="h-4 w-4 text-red-500" />
                                  </Button>
                                </div>
                              </CardContent>
                            </Card>
                          ))
                        )}
                      </div>
                    </TabsContent>

                    {/* Tab 3: Nhp S Lng Ln */}
                    <TabsContent value="bulkImport" className="mt-4">
                      <form onSubmit={handleBulkImport} className="grid gap-4">
                        <Label htmlFor="bulkData" className="text-sm font-medium">
                          Nhp t vng (mi t mt dng, phn tch bng du phy)
                        </Label>
                        <p className="text-xs text-gray-500 dark:text-gray-400 -mt-2">
                          nh dng: **Kanji/T, Kana/Phin m, Ngha, Ghi ch (ty chn)**
                        </p>
                        <Textarea
                          id="bulkData"
                          rows={8}
                          value={bulkData}
                          onChange={(e) => setBulkData(e.target.value)}
                          placeholder="V d:&#10;, , n&#10;, , Ting Nht, Ghi ch quan trng"
                          required
                        />
                        {bulkImportProgress && (
                            <div className="flex items-center text-sm text-blue-500">
                                <Loader2 className="h-4 w-4 animate-spin mr-2" /> {bulkImportProgress}
                            </div>
                        )}
                        <Button type="submit" disabled={isLoading || bulkImportProgress !== null || !bulkData.trim()}>
                          <Import className="h-4 w-4 mr-2" /> Nhp {bulkData.trim().split('\n').filter(line => line.trim() !== '').length} T
                        </Button>
                      </form>
                    </TabsContent>

                  </Tabs>
                </CardContent>
              </Card>
            ) : (
              <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg text-center p-8 h-full flex flex-col justify-center items-center">
                <BookOpen className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-medium mb-2">Chn mt b t</h3>
                <p className="text-gray-600 dark:text-gray-400 mb-4">
                  Vui lng chn mt b t t danh sch bn tri hoc to b t mi.
                </p>
                <Button variant="outline" onClick={fetchSets} disabled={isLoading}>
                  {isLoading ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : 'Ti li danh sch b t'}
                </Button>
              </Card>
            )}
          </div>
        </div>
      {/* TH <p> C DI CHUYN VO TRONG TH DIV GC */}
      <p className="mt-8 text-sm text-center text-gray-500 dark:text-gray-500">
          *  kh (Difficulty) s c cp nht t ng sau mi bui hc.
      </p>

      {/* DIALOG CHNH SA T */}
      <Dialog open={isEditWordOpen} onOpenChange={setIsEditWordOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Chnh sa T Vng</DialogTitle>
            <DialogDescription>
              Cp nht thng tin cho t "{editingWord?.kanji}".
            </DialogDescription>
          </DialogHeader>
          <form onSubmit={handleUpdateWord} className="grid gap-4 py-4">
            <div className="grid gap-2">
              <Label htmlFor="editKanji">Kanji/T <span className="text-red-500">*</span></Label>
              <Input
                id="editKanji"
                value={editKanji}
                onChange={(e) => setEditKanji(e.target.value)}
                required
              />
            </div>
            <div className="grid gap-2">
              <Label htmlFor="editKana">Kana/Phin m</Label>
              <Input
                id="editKana"
                value={editKana}
                onChange={(e) => setEditKana(e.target.value)}
              />
            </div>
            <div className="grid gap-2">
              <Label htmlFor="editMeaning">Ngha <span className="text-red-500">*</span></Label>
              <Input
                id="editMeaning"
                value={editMeaning}
                onChange={(e) => setEditMeaning(e.target.value)}
                required
              />
            </div>
            {/*  PHN B THIU  C B SUNG  Y */}
            <div className="grid gap-2">
              <Label htmlFor="editNotes">Ghi ch (Ty chn)</Label>
              <Textarea
                id="editNotes"
                value={editNotes}
                onChange={(e) => setEditNotes(e.target.value)}
              />
            </div>
            <Button type="submit" disabled={isLoading || !editKanji.trim() || !editMeaning.trim()}>
              {isLoading ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : 'Lu Thay i'}
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  )
}


// Component con cho vic chnh sa b t (gi nguyn logic)
interface EditSetFormProps {
  set: VocabularySet;
  onSave: (id: string, newName: string, newDescription: string) => Promise<void>;
  isLoading: boolean;
}

const EditSetForm: React.FC<EditSetFormProps> = ({ set, onSave, isLoading }) => {
  const [name, setName] = useState(set.name);
  const [description, setDescription] = useState(set.description || '');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(set.id, name, description);
  };

  return (
    <form onSubmit={handleSubmit} className="grid gap-4 py-4">
      <div className="grid gap-2">
        <Label htmlFor="editSetName">Tn b t <span className="text-red-500">*</span></Label>
        <Input
          id="editSetName"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
      </div>
      <div className="grid gap-2">
        <Label htmlFor="editSetDescription">M t</Label>
        <Textarea
          id="editSetDescription"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          placeholder="M t ngn gn v b t ny"
        />
      </div>
      <Button type="submit" disabled={isLoading || !name.trim()}>
        {isLoading ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : 'Lu B T'}
      </Button>
    </form>
  );
};