import { Outlet, Link, useLocation } from 'react-router-dom'; 
import { LogOut, Home, Settings, Brain, BookOpen, BarChart3, Menu, X, User as UserIcon, Loader2 } from 'lucide-react'; 
import { useState, useEffect } from 'react';

import { useIsMobile } from '../hooks/use-mobile.tsx';
import { Button } from './ui/button';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card'; // Import Card components
import { useAuth } from './AuthProvider'; 

export default function Shell() {
  // Ly thm showAuthModal v showAuthForm
  const { user, logout, showAuthModal, loading, showAuthForm } = useAuth(); 
  const isMobile = useIsMobile();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const location = useLocation();
  
  const handleLogout = () => {
    logout(); 
    setIsMenuOpen(false); 
  };

  // Cc ng dn cn bo v
  const protectedRoutes = ['/learn', '/vocabulary', '/statistics', '/settings'];
  const isProtected = protectedRoutes.includes(location.pathname);


  //  IU CHNH LOGIC BO V ROUTE V HIN TH AUTH MODAL
  useEffect(() => {
    // Logic: Nu khng trong qu trnh ti, ngi dng cha ng nhp, v ang  trang bo v:
    if (!loading && !user && isProtected) {
      // Ch gi showAuthModal(true)
      showAuthModal(true); 
    } else if (user && isProtected && showAuthForm) {
      // Nu  ng nhp v ang  trang bo v, nhng modal vn m (trng hp user va ng nhp thnh cng):
      showAuthModal(false); 
    }
    
    // Nu chuyn n trang khng c bo v, m bo modal b n
    if (!isProtected && showAuthForm) {
        showAuthModal(false);
    }
    // Ghi ch: Thm showAuthForm vo dependency  fix li
  }, [loading, user, isProtected, showAuthModal, location.pathname, showAuthForm]); 


  // Logic Nav Items (gi nguyn)
  const navItems = [
    { to: '/', label: 'Trang ch', icon: Home, isProtected: false },
    { to: '/learn', label: 'Ch  hc', icon: Brain, isProtected: true },
    { to: '/vocabulary', label: 'Qun l t vng', icon: BookOpen, isProtected: true },
    { to: '/statistics', label: 'Thng k', icon: BarChart3, isProtected: true },
    { to: '/settings', label: 'Ci t', icon: Settings, isProtected: true },
  ];

  // LOGIC IU KIN RENDER
  // Ch render Outlet nu: Qu trnh ti kt thc V (Route KHNG bo v HOC Ngi dng  ng nhp)
  const shouldRenderOutlet = !loading && (!isProtected || user);
  
  
  // ... (MobileMenu v Sidebar gi nguyn) ...
  // Mobile Menu UI (Gi nguyn)
  const MobileMenu = ({ navItems, isMenuOpen, setIsMenuOpen, handleLogout, user }: any) => (
    <div 
        className={`fixed inset-0 z-40 lg:hidden transition-opacity duration-300 ${isMenuOpen ? 'opacity-100 visible' : 'opacity-0 invisible'}`}
        aria-hidden={!isMenuOpen}
    >
        {/* Overlay */}
        <div className="absolute inset-0 bg-black/50" onClick={() => setIsMenuOpen(false)}></div>
        
        {/* Drawer */}
        <div 
            className={`absolute left-0 top-0 h-full w-64 bg-white dark:bg-gray-900 shadow-2xl transition-transform duration-300 transform ${isMenuOpen ? 'translate-x-0' : '-translate-x-full'} p-4`}
        >
            <div className="flex flex-col h-full">
                <div className="flex justify-between items-center mb-6">
                    <h2 className="text-xl font-bold text-primary">Nihongo App</h2>
                    <Button variant="ghost" size="icon" onClick={() => setIsMenuOpen(false)}>
                        <X className="h-5 w-5" />
                    </Button>
                </div>
            
                {/* User Info Mobile */}
                <div className="mb-4 p-3 bg-accent dark:bg-gray-800 rounded-lg">
                    {user ? (
                        <div className="flex items-center space-x-3">
                            <UserIcon className="h-6 w-6 text-primary" />
                            <div>
                                <p className="font-semibold text-sm truncate">{user.name || user.email}</p>
                                <p className="text-xs text-gray-500 dark:text-gray-400">im: {user.totalPoints || 0}</p>
                            </div>
                        </div>
                    ) : (
                        <Button onClick={() => {
                          showAuthModal(true);
                          setIsMenuOpen(false);
                        }} className="w-full">
                            <UserIcon className="h-4 w-4 mr-2" /> ng nhp
                        </Button>
                    )}
                </div>
            
                {/* Nav Items cho Mobile */}
                <nav className="flex-1 space-y-1">
                    {navItems.map((item: any) => (
                        <Link
                            key={item.to}
                            to={item.to}
                            onClick={() => setIsMenuOpen(false)}
                            className={`flex items-center p-3 rounded-lg transition-colors ${
                                location.pathname === item.to 
                                    ? 'bg-primary text-primary-foreground font-semibold' 
                                    : 'text-foreground hover:bg-accent dark:hover:bg-gray-700'
                                }`}
                        >
                            <item.icon className="h-5 w-5 mr-3 shrink-0" />
                            <span className="text-sm">{item.label}</span>
                        </Link>
                    ))}
                </nav>

                {/* Logout button Mobile */}
                {user && (
                    <div className="mt-auto pt-4 border-t dark:border-gray-700">
                        <Button 
                            variant="outline" 
                            className="w-full bg-transparent hover:bg-red-50 dark:hover:bg-red-900/50"
                            onClick={handleLogout}
                        >
                            <LogOut className="h-4 w-4 mr-2" /> 
                            ng xut
                        </Button>
                    </div>
                )}
            </div>
        </div>
    </div>
  )

  // Sidebar Desktop UI (Gi nguyn)
  const Sidebar = ({ navItems, handleLogout, user }: any) => (
    <div className="hidden lg:flex lg:flex-shrink-0">
      <div className="flex flex-col w-64 border-r dark:border-gray-800 bg-white dark:bg-gray-900">
        <div className="flex flex-col flex-1 h-0">
          <div className="flex flex-col flex-1 overflow-y-auto pt-5 pb-4 px-4">
            <div className="flex items-center flex-shrink-0 px-2 mb-6">
              <h1 className="text-2xl font-bold text-primary">Nihongo App</h1>
            </div>
            
            {/* User Info Desktop */}
            <div className="mb-6 p-3 bg-accent dark:bg-gray-800 rounded-lg">
              {user ? (
                  <div className="flex items-center space-x-3">
                      <img 
                          src={user.photoURL || `https://api.dicebear.com/7.x/initials/svg?seed=${user.name || 'User'}`} 
                          alt="Avatar" 
                          className="h-8 w-8 rounded-full object-cover" 
                      />
                      <div>
                          <p className="font-semibold text-sm truncate">{user.name || user.email}</p>
                          <p className="text-xs text-gray-500 dark:text-gray-400">im: {user.totalPoints || 0}</p>
                      </div>
                  </div>
              ) : (
                  <Button onClick={() => showAuthModal(true)} className="w-full">
                      <UserIcon className="h-4 w-4 mr-2" /> ng nhp
                  </Button>
              )}
            </div>

            {/* Nav Items Desktop */}
            <nav className="flex-1 space-y-1">
              {navItems.map((item: any) => (
                <Link
                  key={item.to}
                  to={item.to}
                  className={`flex items-center p-3 rounded-lg transition-colors ${
                    location.pathname === item.to 
                      ? 'bg-primary text-primary-foreground font-semibold' 
                      : 'text-foreground hover:bg-accent dark:hover:bg-gray-700'
                    }`}
                >
                  <item.icon className="h-5 w-5 mr-3 shrink-0" />
                  <span className="text-sm">{item.label}</span>
                </Link>
              ))}
            </nav>
          </div>
          
          {/* Logout button Desktop */}
          {user && (
             <div className="pb-4 px-4 border-t dark:border-gray-800">
                <Button 
                  variant="outline" 
                  className="w-full bg-transparent hover:bg-red-50 dark:hover:bg-red-900/50"
                  onClick={handleLogout}
                >
                  <LogOut className="h-4 w-4 mr-2" /> 
                  ng xut
                </Button>
             </div>
          )}
        </div>
      </div>
    </div>
  )


  // Header Mobile UI (Gi nguyn)
  const HeaderMobile = ({ isMobile, isMenuOpen, setIsMenuOpen, user }: any) => {
    if (!isMobile) return null;
    return (
      <header className="fixed top-0 left-0 right-0 z-30 flex items-center justify-between p-4 bg-white dark:bg-gray-900 border-b dark:border-gray-800 lg:hidden">
        <Button variant="ghost" size="icon" onClick={() => setIsMenuOpen(true)}>
          <Menu className="h-6 w-6" />
        </Button>
        <h1 className="text-xl font-bold text-primary">Nihongo App</h1>
        <div className="w-10">
          {user ? (
            <img 
                src={user.photoURL || `https://api.dicebear.com/7.x/initials/svg?seed=${user.name || 'User'}`} 
                alt="Avatar" 
                className="h-8 w-8 rounded-full object-cover" 
            />
          ) : (
            <Button variant="ghost" size="icon" onClick={() => showAuthModal(true)}>
                <UserIcon className="h-6 w-6" />
            </Button>
          )}
        </div>
      </header>
    )
  }

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-900">
      
      {/* Sidebar (Desktop) */}
      <Sidebar navItems={navItems} handleLogout={handleLogout} user={user} />
      
      {/* Mobile Menu */}
      <MobileMenu 
        navItems={navItems} 
        isMenuOpen={isMenuOpen} 
        setIsMenuOpen={setIsMenuOpen} 
        handleLogout={handleLogout} 
        user={user}
      />
      
      {/* Header Mobile */}
      <HeaderMobile isMobile={isMobile} isMenuOpen={isMenuOpen} setIsMenuOpen={setIsMenuOpen} user={user} />

      <main className="flex-1 overflow-y-auto pt-0 lg:pt-0">
        
        {loading ? (
          <div className="flex h-full items-center justify-center p-8">
            <Loader2 className="h-6 w-6 animate-spin text-primary" />
            <span className="ml-2 text-gray-500">ang ti b cc...</span>
          </div>
        ) : (
          // RENDER C IU KIN
          shouldRenderOutlet ? (
            <div className={`p-4 md:p-8 ${isMobile ? 'pt-20' : ''}`}>
                <Outlet /> {/* Render ni dung trang con */}
            </div>
          ) : (
             // Khi ngi dng cha ng nhp,  protected route
             isProtected && !user ? (
                //  RENDER CARD YU CU NG NHP (TRNH RENDER LI NU MODAL ANG M)
                <div className="flex h-full items-center justify-center p-8">
                    <Card className="text-center shadow-lg w-full max-w-sm">
                        <CardHeader>
                            <CardTitle>Truy cp b t chi</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-gray-500 mb-4">Vui lng ng nhp  truy cp trang ny.</p>
                            <Button onClick={() => showAuthModal(true)}>ng nhp ngay</Button>
                        </CardContent>
                    </Card>
                </div>
             ) : (
                // Nu ang  trang khng bo v (v d: /)
                <div className={`p-4 md:p-8 ${isMobile ? 'pt-20' : ''}`}>
                    <Outlet /> 
                </div>
             )
          )
        )}
      </main>
    </div>
  );
}