/**
 * Settings Page
 * Manages user preferences and learning configurations
 */

import { useState, useEffect } from 'react'
import { Button } from '../components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card'
import { Input } from '../components/ui/input'
import { Label } from '../components/ui/label'
import { Switch } from '../components/ui/switch'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs'
import { Separator } from '../components/ui/separator'
import { 
  Settings as SettingsIcon, 
  Timer, 
  Target, 
  Bell,
  Moon,
  Sun,
  LogOut,
  User
} from 'lucide-react'

//  IMPORT useAuth
import { useAuth } from '../components/AuthProvider'

export default function Settings() {
  //  LY THNG TIN USER V HM LOGOUT T AUTH CONTEXT
  const { user, logout } = useAuth()
  
  const [settings, setSettings] = useState({
    timer: 15, // seconds per question
    wordsPerSession: 10,
    dailyGoal: 50,
    reminderEnabled: true,
    reminderTime: '20:00',
    soundEnabled: true,
    vibrationEnabled: true,
    darkMode: false // S s dng theme context nu c
  })

  //  B userInfo v  dng user t AuthContext
  // const [userInfo, setUserInfo] = useState({
  //   name: 'Ngi dng',
  //   email: 'user@example.com'
  // })

  useEffect(() => {
    // Load settings from localStorage or backend
    // V d:
    // const savedSettings = JSON.parse(localStorage.getItem('userSettings') || '{}')
    // setSettings(prev => ({...prev, ...savedSettings}))
  }, [])
  
  const handleSettingChange = (key: keyof typeof settings, value: any) => {
    setSettings(prev => ({ ...prev, [key]: value }))
    // Lu ci t vo backend/localStorage
    // saveSettingsToBackend({...settings, [key]: value})
  }

  const handleLogout = () => {
    logout() // Gi hm logout t AuthProvider
  }
  
  // Hin th loading/placeholder nu user cha load
  if (!user) {
    return (
      <div className="flex items-center justify-center h-full p-4">
        <div className="animate-spin rounded-full h-10 w-10 border-b-2 border-primary"></div>
        <p className="ml-3 text-lg text-primary">ang ti ci t...</p>
      </div>
    )
  }

  return (
    <div className="min-h-full p-4 sm:p-6 max-w-3xl mx-auto">
      <header className="mb-8">
        <h1 className="text-3xl font-bold flex items-center">
          <SettingsIcon className="h-6 w-6 mr-2 text-primary" /> Ci t ng dng
        </h1>
        <p className="text-muted-foreground mt-1">Qun l ti khon v ty chnh tri nghim hc tp ca bn.</p>
      </header>

      <div className="space-y-8">
        <Tabs defaultValue="account" className="w-full">
          <TabsList className="grid w-full grid-cols-3 md:grid-cols-4">
            <TabsTrigger value="account">Ti khon</TabsTrigger>
            <TabsTrigger value="learning">Hc tp</TabsTrigger>
            <TabsTrigger value="notifications">Thng bo</TabsTrigger>
            <TabsTrigger value="appearance">Giao din</TabsTrigger>
          </TabsList>
          
          {/* ====================== TI KHON ====================== */}
          <TabsContent value="account" className="mt-6">
            <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg">
              <CardHeader>
                <CardTitle>Thng tin C nhn</CardTitle>
                <CardDescription>Cp nht tn v a ch email ca bn.</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Tn</Label>
                  <Input 
                    id="name" 
                    defaultValue={user.name || ''} 
                    placeholder="Tn ca bn"
                    disabled // Gi nh vic i tn s c x l qua form ring
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="email">Email</Label>
                  <Input 
                    id="email" 
                    defaultValue={user.email} 
                    placeholder="Email"
                    type="email"
                    disabled // Email thng khng c i
                  />
                </div>
                {/* <Button>Lu Thay i</Button> */}
              </CardContent>
            </Card>
            
            {/* Logout button -  chuyn xung cui cng, nhng gi li nu mun c trong tab */}
            {/* <Button 
              variant="outline" 
              className="w-full mt-6 bg-transparent"
              onClick={handleLogout}
            >
              <LogOut className="h-4 w-4 mr-2" />
              ng xut
            </Button> */}
          </TabsContent>
          
          {/* ====================== HC TP ====================== */}
          <TabsContent value="learning" className="mt-6 space-y-6">
            <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg">
              <CardHeader>
                <CardTitle className="flex items-center">
                  <Timer className="h-5 w-5 mr-2 text-secondary" /> Cu hnh Bi hc
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="words-per-session">S t mi phin</Label>
                  <Input
                    id="words-per-session"
                    type="number"
                    value={settings.wordsPerSession}
                    onChange={(e) => handleSettingChange('wordsPerSession', parseInt(e.target.value))}
                    min={5}
                    max={50}
                  />
                  <p className="text-xs text-muted-foreground">S lng t vng bn s hc trong mi phin.</p>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="timer">Gii hn thi gian mi cu (giy)</Label>
                  <Input
                    id="timer"
                    type="number"
                    value={settings.timer}
                    onChange={(e) => handleSettingChange('timer', parseInt(e.target.value))}
                    min={5}
                    max={60}
                  />
                  <p className="text-xs text-muted-foreground">t thnh 0  khng gii hn thi gian.</p>
                </div>
                <Separator />
                <div className="space-y-2">
                  <Label htmlFor="daily-goal">Mc tiu t vng hng ngy</Label>
                  <Input
                    id="daily-goal"
                    type="number"
                    value={settings.dailyGoal}
                    onChange={(e) => handleSettingChange('dailyGoal', parseInt(e.target.value))}
                    min={1}
                  />
                  <p className="text-xs text-muted-foreground">S t bn mun hc hoc n tp mi ngy.</p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
          
          {/* ====================== THNG BO ====================== */}
          <TabsContent value="notifications" className="mt-6 space-y-6">
            <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg">
              <CardHeader>
                <CardTitle className="flex items-center">
                  <Bell className="h-5 w-5 mr-2 text-secondary" /> Ci t Thng bo
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center justify-between">
                  <Label htmlFor="reminder-enabled" className="flex flex-col space-y-1">
                    <span>Nhc nh hc hng ngy</span>
                    <span className="font-normal text-muted-foreground">Nhn thng bo  duy tr streak.</span>
                  </Label>
                  <Switch
                    id="reminder-enabled"
                    checked={settings.reminderEnabled}
                    onCheckedChange={(checked) => handleSettingChange('reminderEnabled', checked)}
                  />
                </div>
                {settings.reminderEnabled && (
                  <div className="space-y-2">
                    <Label htmlFor="reminder-time">Thi gian nhc nh</Label>
                    <Input
                      id="reminder-time"
                      type="time"
                      value={settings.reminderTime}
                      onChange={(e) => handleSettingChange('reminderTime', e.target.value)}
                    />
                  </div>
                )}
                <Separator />
                <div className="flex items-center justify-between">
                  <Label htmlFor="sound-enabled" className="flex flex-col space-y-1">
                    <span>m thanh trong ng dng</span>
                    <span className="font-normal text-muted-foreground">Bt/tt m thanh phn hi.</span>
                  </Label>
                  <Switch
                    id="sound-enabled"
                    checked={settings.soundEnabled}
                    onCheckedChange={(checked) => handleSettingChange('soundEnabled', checked)}
                  />
                </div>
                <div className="flex items-center justify-between">
                  <Label htmlFor="vibration-enabled" className="flex flex-col space-y-1">
                    <span>Rung (ch dnh cho thit b di ng)</span>
                    <span className="font-normal text-muted-foreground">Phn hi rung cho cc hnh ng quan trng.</span>
                  </Label>
                  <Switch
                    id="vibration-enabled"
                    checked={settings.vibrationEnabled}
                    onCheckedChange={(checked) => handleSettingChange('vibrationEnabled', checked)}
                  />
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* ====================== GIAO DIN ====================== */}
          <TabsContent value="appearance" className="mt-6 space-y-6">
            <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg">
              <CardHeader>
                <CardTitle className="flex items-center">
                  <Sun className="h-5 w-5 mr-2 text-secondary" /> Ty chnh Giao din
                </CardTitle>
                <CardDescription>Thay i giao din ng dng gia Sng v Ti.</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center justify-between">
                  <Label htmlFor="dark-mode" className="flex items-center space-x-2">
                    {settings.darkMode ? (
                      <Moon className="h-5 w-5" />
                    ) : (
                      <Sun className="h-5 w-5" />
                    )}
                    <span>Ch  Ti (Dark Mode)</span>
                  </Label>
                  <Switch
                    id="dark-mode"
                    checked={settings.darkMode}
                    onCheckedChange={(checked) => handleSettingChange('darkMode', checked)}
                  />
                </div>
              </CardContent>
            </Card>

            <Card className="bg-white dark:bg-gray-800 border-0 shadow-lg">
              <CardHeader>
                <CardTitle>Kch thc ch</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <Button variant="outline" className="w-full justify-start bg-transparent">
                    <span className="text-sm">Nh</span>
                  </Button>
                  <Button variant="outline" className="w-full justify-start bg-transparent">
                    <span className="text-base">Va (mc nh)</span>
                  </Button>
                  <Button variant="outline" className="w-full justify-start bg-transparent">
                    <span className="text-lg">Ln</span>
                  </Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

        </Tabs>
        
        {/* Logout button chnh (sau tt c cc tab) */}
        <Button 
          variant="outline" 
          className="w-full mt-6 bg-transparent hover:bg-red-50 dark:hover:bg-red-900/50 text-red-600 dark:text-red-400 border-red-300 dark:border-red-700"
          onClick={handleLogout}
        >
          <LogOut className="h-4 w-4 mr-2" />
          ng xut khi Nihongo Navigator
        </Button>
      </div>
    </div>
  )
}