/**
 * Learning Mode Component
 * Implements smart learning algorithm with difficulty scoring
 * Displays vocabulary questions with 4 answer options
 */

import { useState, useEffect, useMemo, useCallback } from 'react'
import { Button } from './ui/button'
import { Card, CardContent, CardHeader, CardTitle } from './ui/card'
import { Progress } from './ui/progress'
import { Badge } from './ui/badge'
import { 
  CheckCircle, 
  XCircle, 
  RotateCcw, 
  BookOpen, 
  Brain,
  Target,
  Clock, 
  Zap,
  Loader2 // Thm Loader2
} from 'lucide-react'
import { Link, useNavigate } from 'react-router-dom' // Thm Link v useNavigate
import { useAuth } from './AuthProvider' 
//  IMPORT HM FIREBASE THC T
import { getVocabularyWords, saveLearningSession } from '../lib/firebase' 
// Sa i import  s dng giao din (interfaces) t firebase.ts
import type { LearningSessionHistory } from '../lib/firebase'

// ----------------------------------------------------
// INTERFACES
// ----------------------------------------------------
interface VocabularyWord {
  id: string
  kanji: string
  kana: string
  meaning: string
  notes?: string
  difficulty: number // Score from 0-100, lower means more difficult
}

interface LearningSession {
  words: VocabularyWord[]
  currentIndex: number
  correctAnswers: number
  totalAttempts: number
  sessionPoints: number
  timeSpent: number // Tnh bng giy
  startTime: number // Dng cho tnh ton thi gian thc
}

// ----------------------------------------------------
// COMPONENT TM TT PHIN HC (NEW)
// ----------------------------------------------------
interface SessionSummaryProps {
  session: LearningSession
  onRestart: () => void
  onNavigate: (path: string) => void
}

const SessionSummary: React.FC<SessionSummaryProps> = ({ session, onRestart, onNavigate }) => {
  const finalScore = session.sessionPoints
  const totalWords = session.words.length
  const accuracy = totalWords > 0 
    ? Math.round((session.correctAnswers / session.totalAttempts) * 100) 
    : 0

  return (
    <Card className="max-w-xl mx-auto mt-10 p-8 shadow-2xl bg-white dark:bg-gray-800 border-t-4 border-primary">
      <CardHeader className="text-center">
        <BookOpen className="h-12 w-12 text-primary mx-auto mb-4" />
        <CardTitle className="text-3xl font-extrabold text-primary">
          Hon thnh Bui Hc!
        </CardTitle>
        <p className="text-gray-500 dark:text-gray-400">
          Bn  hon thnh **{totalWords}** t vng trong phin ny.
        </p>
      </CardHeader>
      <CardContent className="mt-4 space-y-4">
        <div className="flex justify-between items-center p-3 bg-green-50/50 dark:bg-green-900/50 rounded-lg">
          <Zap className="h-5 w-5 text-green-600" />
          <span className="text-lg font-bold text-green-700 dark:text-green-300">
            +{finalScore} im
          </span>
        </div>
        <div className="grid grid-cols-2 gap-4 text-center">
          <StatCard icon={<Target className="h-5 w-5 text-blue-500" />} label="Chnh xc" value={`${accuracy}%`} />
          <StatCard icon={<Clock className="h-5 w-5 text-indigo-500" />} label="Thi gian" value={`${session.timeSpent}s`} />
        </div>

        <div className="flex flex-col space-y-3 pt-4">
          <Button onClick={onRestart} className="w-full">
            <RotateCcw className="h-4 w-4 mr-2" /> Hc li b t ny
          </Button>
          <Button variant="outline" onClick={() => onNavigate('/statistics')} className="w-full">
            <BarChart3 className="h-4 w-4 mr-2" /> Xem Thng k
          </Button>
          <Button variant="outline" onClick={() => onNavigate('/vocabulary')} className="w-full">
            <BookOpen className="h-4 w-4 mr-2" /> Qun l T vng
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}

interface StatCardProps {
  icon: React.ReactNode;
  label: string;
  value: string;
}

const StatCard: React.FC<StatCardProps> = ({ icon, label, value }) => (
  <div className="p-3 border rounded-lg flex flex-col items-center">
    {icon}
    <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">{label}</p>
    <p className="text-xl font-semibold mt-1">{value}</p>
  </div>
)

// ----------------------------------------------------
// COMPONENT CHNH
// ----------------------------------------------------

// D liu mockWords  b loi b v thay th bng state

export default function LearningMode() {
  const { user } = useAuth()
  const navigate = useNavigate()
  
  // --- TRNG THI MI ---
  // LU : V KHNG C ROUTER ID, TI DNG PLACEHOLDER. 
  // BN PHI LY ID THC T T useParams() HOC STATE KHI B T C CHN.
  const [selectedSetId, setSelectedSetId] = useState<string>('SET_ID_PLACEHOLDER'); 
  const [wordsToLearn, setWordsToLearn] = useState<VocabularyWord[]>([]); 
  const [isLoading, setIsLoading] = useState(true);
  
  const [showResult, setShowResult] = useState<'correct' | 'incorrect' | null>(null)
  const [showSummary, setShowSummary] = useState(false)
  
  const initialSession: LearningSession = useMemo(() => ({
    words: wordsToLearn,
    currentIndex: 0,
    correctAnswers: 0,
    totalAttempts: 0,
    sessionPoints: 0,
    timeSpent: 0,
    startTime: Date.now()
  }), [wordsToLearn]);

  const [session, setSession] = useState<LearningSession>(initialSession); 
  
  // Reset session khi wordsToLearn thay i (Sau khi fetch ln u)
  useEffect(() => {
      setSession(initialSession);
  }, [initialSession]);

  // 1. GI FIREBASE  TI T VNG THC T
  useEffect(() => {
    // Ch ti khi user.id c, c selectedSetId, v cha c t vng
    if (!user?.id || !selectedSetId || wordsToLearn.length > 0) return; 

    const fetchWords = async () => {
        setIsLoading(true);
        try {
            const fetchedWords = await getVocabularyWords(user.id, selectedSetId);
            
            // Xo trn v ch ly 10 t (cu hnh mc nh)
            const shuffledWords = fetchedWords
                                .sort(() => 0.5 - Math.random())
                                .slice(0, 10); 
            
            setWordsToLearn(shuffledWords);
            
        } catch (error) {
            console.error(error);
            // TODO: Hin th toast li
        } finally {
            setIsLoading(false);
        }
    };

    fetchWords();
  }, [user?.id, selectedSetId]); 
  
  // Logic  to cc la chn tr li sai
  const currentWord = session.words[session.currentIndex]
  const answerOptions = useMemo(() => {
    if (!currentWord) return []

    // Ly 3 t vng khc  lm cu tr li sai (u tin cc t cng b t)
    const incorrectWords = session.words
      .filter(w => w.id !== currentWord.id)
      .sort(() => 0.5 - Math.random()) // Xo trn ngu nhin
      .slice(0, 3) 
      .map(w => w.meaning)

    const options = [
      currentWord.meaning,
      ...incorrectWords,
    ].sort(() => 0.5 - Math.random()) // Xo trn cc la chn

    return options.slice(0, 4) // m bo ch c 4 la chn
  }, [currentWord, session.words])
  
  // Hm x l khi ngi dng tr li
  const handleAnswerClick = (selectedAnswer: string) => {
    if (showResult) return

    const isCorrect = selectedAnswer === currentWord.meaning

    setSession(prev => ({
      ...prev,
      totalAttempts: prev.totalAttempts + 1,
      correctAnswers: isCorrect ? prev.correctAnswers + 1 : prev.correctAnswers,
      sessionPoints: isCorrect ? prev.sessionPoints + 10 : prev.sessionPoints // Gi nh 10 im
    }))

    setShowResult(isCorrect ? 'correct' : 'incorrect')
    
    // TI Y CN CP NHT  KH CA T VNG TRONG FIRESTORE (TODO)
  }

  // Chuyn sang cu hi tip theo
  const handleNextQuestion = useCallback(() => {
    setShowResult(null)
    const isLastQuestion = session.currentIndex >= session.words.length - 1
    
    if (isLastQuestion) {
      // 2. KT THC V LU PHIN HC THC T
      handleFinishSession();
    } else {
      setSession(prev => ({
        ...prev,
        currentIndex: prev.currentIndex + 1,
      }))
    }
  }, [session.currentIndex, session.words.length]);

  // Hm x l kt thc v lu phin hc
  const handleFinishSession = async () => {
    if (!user) return;

    // Tnh ton thi gian thc
    const timeInSeconds = Math.round((Date.now() - session.startTime) / 1000);
    
    const finalSessionData: LearningSessionHistory = {
        id: '', // ID s c Firebase to
        userId: user.id,
        wordsLearned: session.totalAttempts,
        points: session.sessionPoints,
        accuracy: session.totalAttempts > 0 
            ? session.correctAnswers / session.totalAttempts 
            : 0,
        timeSpent: timeInSeconds, 
        date: new Date().toISOString(), // Dng Date object cho hm save
        setId: selectedSetId,
    } as any; // Cast tm thi v SessionData trong firebase.ts thiu setId

    try {
        await saveLearningSession({
             ...finalSessionData,
             date: new Date(finalSessionData.date),
        });
        // TODO: Hin th toast thnh cng: " lu kt qu phin hc!"
        
        // Cp nht state session vi thi gian thc
        setSession(prev => ({ ...prev, timeSpent: timeInSeconds }));

    } catch (error) {
        console.error('Lu phin hc tht bi:', error);
        // TODO: Hin th toast li
    }

    setShowSummary(true); // Hin th summary
  };

  const handleRestart = () => {
    setSession(prev => ({
      ...initialSession,
      words: prev.words.sort(() => 0.5 - Math.random()), // Xo trn li
      startTime: Date.now() // Reset thi gian
    }))
    setShowSummary(false)
  }

  const getScoreColor = () => {
    const accuracy = session.totalAttempts > 0 
      ? session.correctAnswers / session.totalAttempts 
      : 0
    if (accuracy >= 0.75) return 'text-green-500 dark:text-green-400'
    if (accuracy >= 0.5) return 'text-yellow-500 dark:text-yellow-400'
    return 'text-red-500 dark:text-red-400'
  }
  
  // 3. HIN TH TRNG THI TI
  if (isLoading) {
    return (
        <div className="flex h-96 items-center justify-center">
            <Loader2 className="h-8 w-8 animate-spin mr-2 text-primary" />
            <p className="text-gray-500 dark:text-gray-400">ang ti t vng cho phin hc...</p>
        </div>
    );
  }

  if (!isLoading && wordsToLearn.length === 0) {
    return (
        <Card className="max-w-xl mx-auto mt-10">
            <CardHeader><CardTitle>Khng tm thy t vng</CardTitle></CardHeader>
            <CardContent>
                <p>B t ny cha c t vng no, hoc bn cha chn b t. Vui lng thm t vng hoc <Link to="/vocabulary" className="text-primary hover:underline">quay li trang Qun l T vng</Link>.</p>
            </CardContent>
        </Card>
    );
  }

  if (showSummary) {
    return <SessionSummary session={session} onRestart={handleRestart} onNavigate={navigate} />
  }

  // Render chnh
  return (
    <div className="max-w-xl mx-auto p-4 md:p-0">
      <h2 className="text-3xl font-bold mb-6 text-gray-900 dark:text-white">Ch  Hc tp</h2>
      
      {/* Progress Bar */}
      <div className="mb-6">
        <div className="flex justify-between text-sm font-medium mb-1 text-gray-600 dark:text-gray-400">
          <span>Tin trnh</span>
          <span>
            {session.currentIndex + 1} / {session.words.length}
          </span>
        </div>
        <Progress 
          value={((session.currentIndex + 1) / session.words.length) * 100} 
          className="h-2" 
        />
      </div>

      {/* Question Card */}
      <Card className="bg-white dark:bg-gray-800 shadow-xl border-t-4 border-primary">
        <CardHeader>
          <div className="flex justify-between items-center">
             <CardTitle className="text-2xl md:text-3xl font-extrabold text-primary">
                {currentWord?.kanji || currentWord?.kana}
             </CardTitle>
             <Badge variant="secondary" className="text-md">
                <Zap className="h-4 w-4 mr-1 fill-yellow-400 stroke-yellow-500" /> +10 XP
             </Badge>
          </div>
          <CardDescription className="text-lg mt-1">
            {currentWord?.kana && currentWord.kana !== currentWord.kanji ? currentWord.kana : ''}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          
          {/* Answer Options */}
          <div className="space-y-3">
            {answerOptions.map((option, index) => (
              <Button 
                key={index}
                variant="outline"
                className={`w-full h-auto py-3 text-left justify-start text-base font-normal transition-colors
                  ${showResult === 'correct' && option === currentWord.meaning 
                    ? 'bg-green-100 dark:bg-green-900/50 border-green-500 hover:bg-green-100 dark:hover:bg-green-900/50' 
                    : ''}
                  ${showResult === 'incorrect' && option === selectedAnswer 
                    ? 'bg-red-100 dark:bg-red-900/50 border-red-500 hover:bg-red-100 dark:hover:bg-red-900/50' 
                    : ''}
                  ${showResult && option === currentWord.meaning && showResult !== 'correct'
                    ? 'border-green-500' // Highlight p n ng sau khi tr li sai
                    : ''}
                `}
                onClick={() => handleAnswerClick(option)}
                disabled={!!showResult}
              >
                {showResult && (
                  <span className="mr-3">
                    {option === currentWord.meaning ? (
                      <CheckCircle className="h-5 w-5 text-green-500 fill-green-100 dark:fill-green-900" />
                    ) : option === selectedAnswer ? (
                      <XCircle className="h-5 w-5 text-red-500 fill-red-100 dark:fill-red-900" />
                    ) : (
                      <Brain className="h-5 w-5 text-gray-400" />
                    )}
                  </span>
                )}
                <span className="flex-1">{option}</span>
              </Button>
            ))}
          </div>

          {/* Action Buttons */}
          <div className="space-y-3 pt-2">
            {showResult && (
              <Button 
                className="w-full" 
                onClick={handleNextQuestion}
                disabled={session.currentIndex === session.words.length}
              >
                {session.currentIndex < session.words.length - 1 ? 'Cu tip theo' : 'Hon thnh'}
              </Button>
            )}
            
            {!showResult && (
              <Button 
                variant="outline" 
                className="w-full bg-transparent"
                onClick={() => {
                  // Gi nh l tr li sai v chuyn sang cu tip theo
                  handleAnswerClick('Cha bit - cu tr li sai') // Coi nh tr li sai
                }}
              >
                Cha bit
              </Button>
            )}
          </div>

          {/* Session stats */}
          <Card className="mt-6 bg-white/80 dark:bg-gray-800/80 border-0 shadow-lg">
            <CardContent className="p-4">
              <div className="flex justify-between items-center text-sm">
                <div className="flex items-center space-x-2">
                  <Target className="h-4 w-4 text-gray-500" />
                  <span className="text-gray-600 dark:text-gray-300">
                    Chnh xc: {session.correctAnswers}/{session.totalAttempts}
                  </span>
                </div>
                <span className={`font-medium ${getScoreColor()}`}>
                  {session.totalAttempts > 0 
                    ? Math.round((session.correctAnswers / session.totalAttempts) * 100)
                    : 0}%
                </span>
              </div>
            </CardContent>
          </Card>
        </CardContent>
      </Card>
    </div>
  )
}