/**
 * Statistics Page
 * Displays detailed learning progress, achievements, and analytics
 * UPDATED: Fetch user stats and learning sessions from Firestore
 */

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card'
import { Progress } from '../components/ui/progress'
import { Badge } from '../components/ui/badge'
import { Button } from '../components/ui/button'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs'
import { 
  TrendingUp, 
  Trophy, 
  Target, 
  Calendar, 
  Clock,
  Brain,
  BookOpen,
  Flame,
  Award,
  BarChart3,
  Loader2 
} from 'lucide-react'

// 1. IMPORT CC THNH PHN FIREBASE & AUTH
import { useAuth } from '../components/AuthProvider'
//  SA: Thm getLearningSessions
import { getUserStatistics, getLearningSessions } from '../lib/firebase' 
import type { LearningSessionHistory } from '../lib/firebase' // Import type t firebase.ts

// 2. DEFINE INTERFACE V INITIAL STATE
//  SA: Dng LearningSessionHistory  import
interface UserStats {
  totalPoints: number
  totalWordsLearned: number
  currentStreak: number
  bestStreak: number
  lastUpdated: string
}

interface Achievement {
  id: string
  title: string
  description: string
  icon: string // Emojis hoc Lucide Icon
  unlocked: boolean
  progress?: number
  maxProgress?: number
}

// Gi nguyn mockAchievements
const mockAchievements: Achievement[] = [
  { id: '1', title: 'Ngi hc u tin', description: 'Hon thnh phin hc u tin', icon: '', unlocked: false, progress: 1, maxProgress: 1 },
  { id: '2', title: '100 t vng', description: ' hc c 100 t', icon: '', unlocked: false, progress: 50, maxProgress: 100 },
  { id: '3', title: '5 ngy lin tc', description: 'Hc lin tc trong 5 ngy', icon: '', unlocked: false, progress: 3, maxProgress: 5 },
  { id: '4', title: 'Chnh xc 90%', description: 't  chnh xc 90% trong 5 phin hc', icon: '', unlocked: false, progress: 0, maxProgress: 5 },
  { id: '5', title: 'Hng ngn im', description: 't 1000 im tch ly', icon: '', unlocked: false, progress: 0, maxProgress: 1000 },
];

export default function Statistics() {
  const { user } = useAuth()
  
  // State cho Thng k tng th
  const [userStats, setUserStats] = useState<UserStats | null>(null)
  const [isStatsLoading, setIsStatsLoading] = useState(true)
  
  // 3. THM STATE CHO LCH S PHIN HC THC T
  const [learningSessions, setLearningSessions] = useState<LearningSessionHistory[]>([]); 
  const [isSessionsLoading, setIsSessionsLoading] = useState(true);
  
  const [achievements, setAchievements] = useState(mockAchievements)
  
  // Ti thng k ngi dng tng th ( c, gi nguyn)
  useEffect(() => {
    if (!user?.id) return
    const loadUserStats = async () => {
      setIsStatsLoading(true)
      try {
        const statsData = await getUserStatistics(user.id)
        if (statsData) {
          setUserStats(statsData as UserStats)
        }
      } catch (error) {
        console.error('Error loading stats:', error)
      } finally {
        setIsStatsLoading(false)
      }
    }
    loadUserStats()
  }, [user?.id])

  // 4. THM useEffect  TI LCH S PHIN HC
  useEffect(() => {
    if (!user?.id) return;

    const fetchSessions = async () => {
        setIsSessionsLoading(true);
        try {
            // Ly 10 phin hc gn nht
            const sessions = await getLearningSessions(user.id, 10); 
            setLearningSessions(sessions);
        } catch (error) {
            console.error('Error loading learning sessions:', error);
        } finally {
            setIsSessionsLoading(false);
        }
    };

    fetchSessions();
  }, [user?.id]); 

  // Format ngy thng cho lch s
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };
  
  // ... (Gi nguyn logic render)

  return (
    <div className="p-4 md:p-0">
      <h1 className="text-3xl font-bold mb-6 text-gray-900 dark:text-white">
         Thng k Hc tp
      </h1>

      {/* Tng quan */}
      {isStatsLoading ? (
        <div className="flex justify-center items-center h-20">
            <Loader2 className="h-6 w-6 animate-spin text-primary" />
        </div>
      ) : (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
          <StatCard icon={<Trophy className="h-5 w-5 text-yellow-500" />} title="Tng im" value={userStats?.totalPoints || 0} />
          <StatCard icon={<Brain className="h-5 w-5 text-primary" />} title="T  hc" value={userStats?.totalWordsLearned || 0} />
          <StatCard icon={<Flame className="h-5 w-5 text-red-500" />} title="Streak hin ti" value={userStats?.currentStreak || 0} />
          <StatCard icon={<Calendar className="h-5 w-5 text-green-500" />} title="Streak tt nht" value={userStats?.bestStreak || 0} />
        </div>
      )}

      {/* Tabs cho Chi tit */}
      <div className="mt-8">
        <Tabs defaultValue="chart" className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="chart">Biu </TabsTrigger>
            <TabsTrigger value="history">Lch s</TabsTrigger>
            <TabsTrigger value="achievements">Thnh tu</TabsTrigger>
          </TabsList>

          {/* Tab Biu  (Gi nguyn) */}
          <TabsContent value="chart" className="mt-4">
            <Card className="bg-white dark:bg-gray-800 shadow-lg p-6 min-h-[300px]">
                <CardTitle className="text-xl mb-4">Tin trnh hc tp (7 ngy)</CardTitle>
                <p className="text-gray-500 dark:text-gray-400">
                    // TODO: Tch hp th vin recharts  v biu  t d liu LearningSessionHistory.
                    {/*  */}
                </p>
                <div className="flex justify-center items-center h-40">
                    <BarChart3 className="h-10 w-10 text-gray-400 opacity-50" />
                    <p className="ml-4 text-gray-500">Biu  ang c xy dng...</p>
                </div>
            </Card>
          </TabsContent>
          
          {/* 5. CP NHT: Tab Lch s Phin hc */}
          <TabsContent value="history" className="mt-4">
            {isSessionsLoading ? (
                <div className="flex justify-center items-center h-40">
                    <Loader2 className="h-6 w-6 animate-spin mr-2 text-primary" />
                    <p className="text-gray-500">ang ti lch s phin hc...</p>
                </div>
            ) : learningSessions.length === 0 ? (
                <Card className="text-center p-6 bg-white/80 dark:bg-gray-800/80">
                    <CardTitle className="text-lg">Cha c phin hc no</CardTitle>
                    <CardDescription className="mt-2">
                        Hy bt u hc t vng  thy lch s tin trnh ca bn  y!
                    </CardDescription>
                </Card>
            ) : (
                <div className="space-y-4">
                    {learningSessions.map((session, index) => (
                        <Card key={session.id || index} className="bg-white dark:bg-gray-800 shadow-sm transition-shadow hover:shadow-md">
                            <CardContent className="p-4 flex justify-between items-center">
                                <div className="flex items-center space-x-4">
                                    <Clock className="h-6 w-6 text-indigo-500" />
                                    <div>
                                        <p className="font-medium text-gray-800 dark:text-white">
                                            Phin hc ngy {formatDate(session.date)}
                                        </p>
                                        <p className="text-sm text-gray-600 dark:text-gray-400">
                                            {session.wordsLearned} t | {session.timeSpent}s
                                        </p>
                                    </div>
                                </div>
                                <div className="text-right">
                                    <p className="text-lg font-bold text-green-600 dark:text-green-400">
                                        +{session.points} im
                                    </p>
                                    <Badge variant="secondary" className="mt-1">
                                        {Math.round(session.accuracy * 100)}% Chnh xc
                                    </Badge>
                                </div>
                            </CardContent>
                        </Card>
                    ))}
                </div>
            )}
          </TabsContent>

          {/* Tab Thnh tu (Gi nguyn) */}
          <TabsContent value="achievements" className="mt-4">
            <Card className="shadow-lg">
              <CardHeader>
                <CardTitle>ang tin hnh</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {achievements.filter(a => !a.unlocked && a.progress).map((achievement) => (
                    <div key={achievement.id} className="flex items-center space-x-3 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <span className="text-2xl opacity-50">{achievement.icon}</span>
                      <div className="flex-1">
                        <p className="font-medium text-gray-800 dark:text-white">
                          {achievement.title}
                        </p>
                        <p className="text-sm text-gray-600 dark:text-gray-300">
                          {achievement.description}
                        </p>
                        <Progress 
                          value={(achievement.progress! / achievement.maxProgress!) * 100} 
                          className="h-2 mt-2" 
                        />
                        <p className="text-xs text-gray-500 mt-1">
                          {achievement.progress}/{achievement.maxProgress}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}

// StatCard component (Gi nguyn)
interface StatCardProps {
  icon: React.ReactNode
  title: string
  value: number
}

const StatCard: React.FC<StatCardProps> = ({ icon, title, value }) => (
  <Card className="text-center shadow-md bg-white dark:bg-gray-800">
    <CardContent className="p-4">
      <div className="flex items-center justify-center mb-2">
        {icon}
      </div>
      <p className="text-2xl font-bold text-gray-900 dark:text-white">{value.toLocaleString()}</p>
      <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">{title}</p>
    </CardContent>
  </Card>
)